<template>
  <body>
    <div class="container-fluid">
      <div class="row mt-5">
        <div class="col-3">
          <div class="card">
            <div class="card-header text-white bg-dark">
              Tasks
            </div>
            <ul class="list-group list-group-flush">
              <!-- Tasks Headers here -->
              <li class="list-group-item pointer" style="cursor: pointer;" v-for="task in tasks.value" :key="task.id"
                @click="select_task_by_id(task.id)">{{task.id}}</li>
            </ul>
          </div>
        </div>
        <div class="col-9">
          <!-- Tasks cards Here -->
          <div class="container">
            <TaskCard :is="selected_task" :id="selected_task.id" :struct="selected_task.struct"
              :active="selected_task.active" />
          </div>
        </div>
      </div>
    </div>
  </body>
</template>

<script setup>
import { ref, reactive, computed } from "vue";
import io from 'socket.io-client';
import { useToast } from "vue-toastification";
import TaskCard from './components/TaskCard.vue';

const socket = io({
  extraHeaders: {
    'type': 'web'
  }
})
const toast = useToast();
const tasks = reactive({
  value: [{
    "id": "Welcome to VSUI",
    "struct":
      [{ "key": "Welcome to VSUI", "component": "LogElement", "data": ['no data loaded'] }],
    "active": false
  }]
})
const selected_task_index = ref(0)
const selected_task = computed(() => (selected_task_index.value < tasks.value.length) ? tasks.value[selected_task_index.value] : tasks.value[0])
function select_task_by_id(target_id) {
  selected_task_index.value = tasks.value.findIndex(x => x.id === target_id);
}

socket.on('connect', () => {
  console.log('connected')
  toast("connected");
})

socket.on('disconnect', () => {
  console.log('disconnected')
  toast.error('disconnected');
})

socket.on('data_push', (data) => {
  tasks.value = JSON.parse(data.data);
  console.log('got a data push')
})

socket.on('toast', (data) => {
  var txt = data.txt;
  var type = data.type;
  if (type == 'success') {
    toast.success(txt);
  } else if (type == 'error') {
    toast.error(txt);
  } else if (type == 'info') {
    toast.info(txt);
  } else if (type == 'warning') {
    toast.warning(txt);
  }
})
</script>

